* {
  box-sizing: border-box;
}

html {
  line-height: 1.5;
  font-feature-settings: 'tnum';
  font-family: 'Roboto', sans-serif;

  display: flex;
  justify-content: center;
  align-items: center;  
  
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1em;
}

.modal {
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0,0,0,0.8); /* Black w/ opacity */
  /* Modal Content */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  font-size: 28px;
  font-weight: bold;
}

.questionaire {
  font-size: 0.9em;
  /* line-height: 2em; */
  margin-bottom: 120px;
}

.questionBox {
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
  background: #f0f0f0;
  padding: 18px;
  margin-bottom: 32px;
  width: 780px;
}

input[type='radio'] {
  width: 20px;
  height: 20px;
  margin: 0 auto; /* Center horizontally */
}


.answer {
  width: 20%;
  display: flex;
  justify-content: center; /* Center horizontally */
  text-align: center;
}

.questionContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 80%;
  align-items: flex-start;

}

